<template>
  <div class="col-3 d-flex flex-column">
    <h5 class="mt-3">Customer Journey</h5>
    <p>You can use customer Journey to differentiate Answer Sets Steps.</p>
    <v-popover :open="showAddPopover" @show="focusOnInput">
      <button @click="showAddPopover = true" class="btn btn-block btn-primary">
        <i class="las la-plus-circle"></i> Add Step
      </button>
      <template slot="popover">
        <form @submit.prevent="submit" class="d-flex p-2 align-items-center">
          <input
            ref="input"
            :disabled="loading"
            v-model="stepInput"
            class="form-control mr-2"
            placeholder="Step Name"
          />
          <button v-if="!loading" class="btn btn-primary">Submit</button>
          <spinner v-else />
        </form>
      </template>
    </v-popover>
    <loading-overlay :loading="loadingOverlay" class="flex-fill overflow-h">
      <div
        class="step d-flex justify-content-between"
        :key="l.ID"
        v-for="l in list"
        @click="showModal(l)"
      >
        <span>{{l.Name}}</span>
        <i name="more" class="las la-ellipsis-v"></i>
      </div>
    </loading-overlay>

    <modal name="step" height="auto" width="400">
      <div class="p-2">
        <spinner v-if="modalLoading" />
        <template v-else>
          <h4>
            {{editInput}}
            <a @click="confirmRemove" href="#" class="float-right text-danger mr-2">
              <i class="lar la-trash-alt"></i>
            </a>
          </h4>
          <input ref="editInput" class="form-control" v-model="editInput" />
          <button class="btn btn-primary float-right my-2 btn-sm" @click="update">Update</button>
          <div class="clearfix"></div>
        </template>
      </div>
    </modal>
    <v-dialog />
  </div>
</template>
<script>
import axios from "axios";
import Spinner from "vue-simple-spinner";
import auth from "@/services/auth";
import LoadingOverlay from "@/components/Loaders/LoadingOverlay";
export default {
  components: {
    Spinner,
    LoadingOverlay
  },
  data() {
    return {
      list: [],
      stepInput: "",
      showAddPopover: false,
      loading: false,
      name: "",
      editInput: "",
      activeStep: {},
      modalLoading: false,
      loadingOverlay: false
    };
  },
  methods: {
    focusOnInput() {
      setTimeout(() => this.$refs.input.focus(), 100);
    },
    showModal(l) {
      this.modalLoading = false;
      this.activeStep = l;
      this.editInput = l.Name;
      this.$modal.show("step");
      setTimeout(() => {
        this.$refs.editInput.focus();
      }, 100);
    },
    stepNameAlreadyExist() {
      this.$modal.show("dialog", {
        text: `Journey name "${this.stepInput}" already exist!`,
        buttons: [{ title: "Close" }]
      });
    },
    async submit() {
      var foundIndex = this.list.findIndex(x => x.Name == this.stepInput);
      if (this.stepInput && foundIndex < 0) {
        this.loading = true;
        try {
          //console.log("submit"+this.stepInput)
          var res = await axios.post("step/upsert", {
            Name: this.stepInput
          });
          if (res.status == 200) {
            this.list.push({
              ID: res.data.ID,
              Name: res.data.Name
            });
            this.stepInput = "";
            this.showAddPopover = false;
          }
        } catch {
        } finally {
          this.loading = false;
        }
      } else if (foundIndex > 0 && this.stepInput) {
        //console.log("already exist")
        this.stepNameAlreadyExist();
        this.stepInput = "";
        this.showAddPopover = false;
      }
    },
    stepNameAlreadyExistForEdit() {
      this.$modal.show("dialog", {
        text: `Journey name "${this.editInput}" already exist!`,
        buttons: [{ title: "Close" }]
      });
    },
    async update() {
      //console.log("editInput="+this.editInput+"\n Active name="+this.activeStep.Name);
      var foundIndex = this.list.findIndex(x => x.Name == this.editInput);

      //console.log("foundIndex="+foundIndex);

      if (
        this.editInput &&
        this.editInput != this.activeStep.Name &&
        foundIndex < 0
      ) {
        this.modalLoading = true;
        try {
          var res = await axios.post("step/upsert", {
            ID: this.activeStep.ID,
            Name: this.editInput
          });
          if (res.status == 200) {
            var foundIndex = this.list.findIndex(
              x => x.ID == this.activeStep.ID
            );
            this.list[foundIndex].Name = this.editInput;
            this.editInput = "";
            this.$modal.hide("step");
          }
        } catch {
        } finally {
          this.modalLoading = false;
        }
      } else if (
        this.editInput &&
        this.editInput != this.activeStep.Name &&
        foundIndex > 0
      ) {
        this.stepNameAlreadyExistForEdit();
      }
    },
    confirmRemove() {
      this.$modal.show("dialog", {
        text: `Are you sure to delete ${this.activeStep.Name}?`,
        buttons: [
          { title: "Close" },
          {
            title: "Delete",
            handler: () => {
              this.$modal.hide("dialog");
              this.remove();
            }
          }
        ]
      });
    },
    async remove() {
      this.modalLoading = true;
      try {
        var res = await axios.delete(`step?id=${this.activeStep.ID}`);
        if (res.status == 200 && res.data.status == "success") {
          this.list = this.list.filter(x => x.ID != this.activeStep.ID);
          this.$modal.hide("step");
        }
      } catch {
        this.modalLoading = false;
      }
    }
  },
  async mounted() {
    try {
      console.log("good");
      this.loadingOverlay = true;
      var res = await axios.get("steps", auth.getHeader());
      if (res.status == 200) this.list = res.data;
      console.log(this.list);
      this.loadingOverlay = false;
    } catch (e) {
      auth.checkAPIResponse(e.response);
    }
  }
};
</script>
<style>
/* .vue-dialog{
    top: 230px  !important;
    left: 300px !important;
} */
</style>
<style lang="scss" scoped>
@import "~bootstrap/scss/bootstrap.scss";
.more {
  cursor: pointer !important;
  padding: 10px !important;
}
.step {
  padding: 10px;
  font-size: 1.2em;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 0.7em;
  color: $secondary;
  font-weight: 300;
  margin-top: 10px;
  i {
    font-size: 1.5em;
    color: $primary;
    cursor: pointer;
    border-radius: 1em;
    transition: all 0.2s;
    &:hover {
      background: $primary;
      color: $white;
    }
  }
}
.tippy-tooltip.bordered-theme {
  border: 1px solid #0062cc !important;
}
.already-exist {
  top: 230px;
  left: 300px;
}
</style>
